/**
 * Bio component that queries for data
 * with Gatsby's useStaticQuery component
 *
 * See: https://www.gatsbyjs.com/docs/use-static-query/
 */

import * as React from "react"
import { useStaticQuery, graphql } from "gatsby"
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import { faTwitter, faGithub, faTelegram, faLinkedin } from "@fortawesome/free-brands-svg-icons"
import { StaticImage } from 'gatsby-plugin-image'

const Bio = ({avatar}) => {
  const data = useStaticQuery(graphql`
    query BioQuery {
      site {
        siteMetadata {
          author {
            name
            summary
          }
          social {
            twitter
            github
            telegram
            linkedin
          }
        }
      }
    }
  `)

  // Set these values by editing "siteMetadata" in gatsby-config.js
  const author = data.site.siteMetadata?.author
  const social = data.site.siteMetadata?.social

  return (
    <div className="bio">

      {avatar && <div>
        <StaticImage
          src="../images/profile-pic.jpg"
          alt="profile picture"
          width={72}
          height={72}
          quality={95}
          imgStyle={{ borderRadius: '100%' }}
        />
      </div>}

      <div>
        {avatar && (<p className="author-name"><strong>{author.name}</strong></p>)}
        {avatar && (<p className="author-info">{author.summary}</p>)}
        {!avatar && (<p className="media-info">Contact me:</p>)}
        <p className="author-link">
          <a href={`https://twitter.com/${social?.twitter || ``}`}>
            <FontAwesomeIcon icon={faTwitter} />
            &nbsp;Twitter
          </a>
        </p>
        <p className="author-link">
          <a href={`https://github.com/${social?.github || ``}`}>
            <FontAwesomeIcon icon={faGithub} />
            &nbsp;GitHub
          </a>
        </p>
        <p className="author-link">
          <a href={`https://www.linkedin.com/in/${social?.linkedin || ``}`}>
            <FontAwesomeIcon icon={faLinkedin} />
            &nbsp;LinkedIn
          </a>
        </p>
        {/*<p className="author-link">*/}
        {/*  <a href={`https://t.me/${social?.telegram || ``}`}>*/}
        {/*    <FontAwesomeIcon icon={faTelegram} />*/}
        {/*    &nbsp;Telegram*/}
        {/*  </a>*/}
        {/*</p>*/}
      </div>

    </div>
  )
}

export default Bio
